@font-face {
	font-family: 'PFDinDisplayPro';
	src: url(https://lull.com/assets/PFDinDisplayPro-Light.eot);
	src: url(https://lull.com/assets/PFDinDisplayPro-Light.eot?#iefix) format('embedded-opentype'), 
		url(https://lull.com/assets/PFDinDisplayPro-Light.woff) format('woff'), 
		url(https://lull.com/assets/PFDinDisplayPro-Light.ttf) format('truetype'), 
		url(https://lull.com/assets/PFDinDisplayPro-Light.svg#svgPFDinDisplayPro-Light) format('svg');
	font-weight: 300;
	font-style: normal;
}

